.todos {
	width: 80%;
	max-width: var(--max-todos-width);
	min-width: 490px;
	margin-left: auto;
	margin-right: auto;
	padding: 20px;
	display: grid;
	grid-template-columns: auto;
	grid-template-areas:
		"todosList"
		"statline";

	background-color: var(--color-gray-1);
	border-radius: 3%;
	box-shadow: 4px 4px 14px rgba(0, 0, 0, .25);

	transition: 0.4s;

	margin-top: var(--margin-top);
}

.todosList {
	display: grid;
	grid-template-columns: auto;
	grid-template-areas:
		"todo";
	text-align: center;
	font-weight: bold;
}

@media (max-width: 650px) {
	* {
		font-size: 10px;
	}

	.todos {
		width: 90%;
		padding: 10px;
	}
}